import axios from 'axios';
import React, {useEffect, useState} from 'react'
import { useHistory, useParams} from 'react-router-dom';
import styled from 'styled-components';
import './Detail.scss'

let 박스 = styled.div`
    padding: 30px;
`;
let 타이틀 = styled.h4`
    font-size: 20px;
    color: ${props => props.색상}
`;
function Detail(props) {
    let history = useHistory();
    let { id } = useParams();
    let clicked = props.shoes.find(x => x.id==id);
    let [alert, change_alert] = useState(true);
    let [inputData, change_inputData] = useState('');
    useEffect(()=>{
        //axios.get(~)// Detail컴포넌트 로드 시 ajax로 데이터를 가져오고 싶음
        let 타이머 = setTimeout(()=>{
            change_alert(false)
        }, 2000);
        return ()=>{clearTimeout(타이머)}
    }, []);//[]안의 값이 업데이트 될 때만, []를 비워두면 해당 컴포넌트가 로드 될 때 딱 한번만
    /*let clicked = props.shoes.find(function(item){
        return item.id == id
    });*/

    return (
      <div className="container">
         <input onChange={(e)=>{change_inputData(e.target.value)}}></input>
        {
            alert === true
            ? ( <div className="my-alert">
                <p>재고가 얼마 남지 않았습니다.</p>
            </div>)
            : null
        }
      <div className="row">
        <div className="col-md-6">
          <img src="https://codingapple1.github.io/shop/shoes1.jpg" width="100%" />
        </div>
        <div className="col-md-6 mt-4">
          <h4 className="pt-5">{clicked.title}</h4>
          <p>{clicked.content}</p>
          <p>{clicked.price}원</p>
          <button className="btn btn-danger">주문하기</button> 
          <button className="btn btn-danger" onClick={()=>{
            //history.goBack();
            history.push('/');
          }}>뒤로가기</button> 
        </div>
      </div>
    </div> 
    )
  }

  export default Detail;